.intro-fig-container {
    margin-top: 20px;
    margin-bottom: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro-text {
    width: 70%;
    padding: 10px;
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 300;
    text-align: start;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
}

.sample-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 1050px;
    height: 380px;
    margin-top: -35px;
    margin-bottom: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
    position: relative;
}

.sample-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid #c5c5c5;
    border-radius: 20px;
    box-shadow: 2px 4px 4px #c5c5c5;
    background-color: #ffffff;
    margin-top: 20px;
}

.description {
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    padding: 0;
    flex-direction: column;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
}

.instruction {
    width: 250px;
    text-align: center;
    margin-top: -10px;
    color: #ffffff;
    /* border: 2px solid #51829B; */
    /* border-radius: 10px; */
    /* box-shadow: 2px 4px 4px #51829B; */
    /* background-color: #51829B; */
    padding: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    line-height: 0.5px;
    position: absolute;
    bottom: 10px;
}

.instruction button {
    border-radius: 10px;
    border: 2px solid #5B9BD5;
    color: #ffffff;
    background-color: #5B9BD5;
    padding-left: 20px;
    padding-right: 20px;
}

.instruction button:hover {
    border-radius: 10px;
    border: 4px solid #5B9BD5;
    color: #ffffff;
    background-color: #5B9BD5;
    padding-left: 20px;
    padding-right: 20px;
}

.description p {
    margin-top: -20px;
    margin-left: 40px;
    font-size: 16px;
    font-weight: 300;
    text-align: start;
    margin-right: 20px;
}

.description h2 {
    margin-left: 30px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.question-components {
    width: 1000px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-self: center;
}

.component-persona {
    width: 700px;
    padding-left: 20px;
    padding-top: 0px;
    padding-bottom: 0px;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
    /* border: 2px solid #5B9BD5; */
    /* border-radius: 20px; */
    /* box-shadow: 2px 4px 4px #DEEBF7; */
    /* background-color: #DEEBF7; */
}

.component-persona p {
    margin-left: 40px;
    font-size: 16px;
    font-weight: 300;
    text-align: start;
    margin-right: 20px;
}

.component-persona h2 {
    margin-left: 10px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.component-scenario {
    width: 250px;
    padding-left: 10px;
    padding-top: 0px;
    padding-bottom: 0px;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
    /* border: 2px solid #AA87C8;
    border-radius: 20px;
    box-shadow: 2px 4px 4px #E8DAF3; */
    /* background-color: #E8DAF3; */
}

.component-scenario p {
    margin-left: 40px;
    font-size: 16px;
    font-weight: 300;
    text-align: start;
    margin-right: 20px;
}

.component-scenario h2 {
    margin-left: 10px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.question {
    margin-top: 18px;
    margin-bottom: 20px;
    /* width: 70%; */
    padding: 0;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
    flex-direction: column;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
    /* border: 2px solid #c5c5c5; */
    /* border-radius: 20px; */
    /* box-shadow: 2px 4px 4px #c5c5c5; */
    /* background-color: #F2F2F2; */
}

.question p {
    margin-left: 40px;
    font-size: 16px;
    font-weight: 300;
    text-align: start;
    margin-right: 20px;
}

.question h2 {
    margin-left: 30px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.evaluation-table {
    margin-top: 20px;
    margin-bottom: 10px;
}

.results-content {
    width: 70%;
    padding: 10px;
    padding-bottom: 0px;
    margin-bottom: 0px;
    font-size: 22px;
    font-weight: 300;
    text-align: start;
    font-family: "Ubuntu", sans-serif;
    line-height: 1.8;
}

.model-comparison-container {
    overflow-x: auto;
    padding: 15px;
    font-size: 22px;
}

.model-comparison {
    width: 100%;
    border-collapse: collapse;
    text-align: left;
}

.model-comparison th, .model-comparison td {
    padding: 8px 12px;
    border-bottom: 1px solid #ccc;
}

.model-comparison th[colspan] {
    text-align: center;
}

.model-comparison th[rowspan] {
    vertical-align: bottom;
}

.model-comparison-container {
    font-size: 16px;
    line-height: 0.8;
}
  